<template>
    <div class="ui-main-module">
        <transition name="slide-fade">
            <div class="ui-search-area" v-if="showSearch">
                <ul class="ui-main-module-search clearFix">
                    <li>
                        <SearchRequirement label="商品SPU">
                            <el-input size="small" v-model="queryData.productSPU"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商代码">
                            <el-input size="small" v-model="queryData.supplierCode"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品目录">
                            <el-input size="small" v-model="queryData.supplierList"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="审核状态">
                            <el-input size="small" v-model="queryData.auditState"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="驳回原因">
                            <el-input size="small" v-model="queryData.rejectRemarks"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="商品名称">
                            <el-input size="small" v-model="queryData.productName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="供应商名称">
                            <el-input size="small" v-model="queryData.supplierName"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作人员">
                            <el-input size="small" v-model="queryData.operateUser"></el-input>
                        </SearchRequirement>
                    </li>
                    <li>
                        <SearchRequirement label="操作时间">
                            <el-input size="small" v-model="queryData.operateTime"></el-input>
                        </SearchRequirement>
                    </li>
                </ul>
                <div class="ui-Filter-btn">
                    <el-button @click="handleResetQueryInfo()" size="small" icon="icon-zhongzhi">重置</el-button>
                    <el-button size="small" type="primary" @click="handleQueryInfo({limit: pageData.currentList})" icon="icon-chaxun">查询</el-button>
                </div>
            </div>
        </transition>
        <div class="ui-fn-module">
            <span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>
            <!--<el-button-->
            <!--v-if="item.method && item.permit(thisVm)"-->
            <!--v-for="item in buttonList"-->
            <!--size="small"-->
            <!--:type="item.type"-->
            <!--:key="item.value"-->
            <!--@click="item.action(thisVm, item.method)">{{item.name}}</el-button>-->
            <el-button @click="handleClickAudit" type="text" size="small"><i class="icon-shenhe icon-warn"></i>审核</el-button>
            <el-button @click="handleClickReject" type="text" size="small"><i class="icon-bohui icon-danger"></i>驳回</el-button>
        </div>
        <ComTableInfo
                isCheck
                :tableKey="tableKey"
                :tableProps="tableProps"
                :tableData="tableData"
                :tableWidth="tableWidth"
                :showSearch="showSearch"
                @checked="handleSaveCheckInfo">
            <template slot="supplier_code" slot-scope="scope">
                <div class="ui-table--supplier_code">
                    <p>{{scope.row.supplier_code}}</p>
                    <div class="ui-table--supplier_code--flag">
                        <span>试卖</span>
                    </div>
                    <div class="ui-table--icon_btn">
                        <i class="icon--yichangfankui" @click="handleClickEdit(scope.row)"></i>
                    </div>
                </div>
            </template>
            <template slot="supplier_name" slot-scope="scope">
                <div>
                    <p>{{scope.row.supplier_name}}</p>
                    <div class="ui-table--icon_btn">
                        <i class="icon--bohui"></i>
                        <i class="icon--wuliuxinxigenzong"></i>
                    </div>
                </div>
            </template>
            <template slot="shipping" slot-scope="scope">
                <div>
                    <p>{{`${scope.row.shipping_province}${scope.row.shipping_city}`}}</p>
                    <p>{{`${scope.row.shipping_area}${scope.row.shipping_address}`}}</p>
                </div>
            </template>
            <template slot="contacts_info" slot-scope="scope">
                <div>
                    <p><i class="icon-quanxianguanli"></i>{{`${scope.row.contacts}/${scope.row.phone}`}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.qq}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.wechat}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.email}}</p>
                </div>
            </template>
            <template slot="payment_info" slot-scope="scope">
                <div>
                    <p><i class="icon-quanxianguanli"></i>{{`${scope.row.pay_method}(${scope.row.settle_method})`}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.receive_account}}</p>
                    <p><i class="icon-quanxianguanli"></i>{{scope.row.shop_name}}</p>
                </div>
            </template>
        </ComTableInfo>
        <div class="ui-main-module-page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="pageData.currentPage"
                    :page-sizes="pageSizes"
                    :page-size="pageData.currentList"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="pageData.currentTotal">
            </el-pagination>
        </div>
        <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
    </div>
</template>

<script>
    import config from '@/assets/js/config';
    import {
        autoAddTableInx,
        getDefaultTime,
        changeTableInfoProps,
        goToNewPage
    } from '@/services/index'
    import {
        mapActions,
        mapGetters
    } from "vuex";
    import {
        getCityInfo
    } from '@/api/login';
    import {
        getSupplierCreateListInfo,
        changeSupplierState,
        getEditInfo
    } from '@/api/dev_department/supplier_development/supplier_create_list';

    export default {
        data() {
            return {

                //高级筛选
                FilterShow: false,

                // 时间搜索框宽度设置
                searchDateWidth: config.SEARCH_DATE_WIDTH,

                thisVm: {},

                // 搜索缩进
                showSearch: true,

                queryData: {
                    productSPU: '',
                    supplierCode: '',
                    supplierList: '',
                    auditState: '',
                    rejectRemarks: '',
                    productName: '',
                    supplierName: '',
                    operateUser: '',
                    operateTime: '',
                },

                optionList: {
                    supplierTypeList: {},
                    settlementTypeList: {},
                    devUserList: {},
                    deliverAddressList: [],
                    paymentModeList: {},
                    liaisonsList: {},
                    createUserList: {},
                    stateList: {},
                    companyTypeList: {}
                },

                tableData: [],
                tableKey: [],
                tableWidth: [],
                tableProps: [],

                // 保存勾选数据
                saveCheckInfo: [],

                //分页
                pageSizes: config.pageData.pageSizes,
                pageData: {
                    currentPage: 1,
                    currentList: 20,
                    currentTotal: 0,
                },

                // 预览图片
                isShowLookImage: false,
                lookImageUrl: []
            }
        },
        computed: {
            ...mapGetters([
                'buttonList',
                'queryParamsInfo',
                'productDevListInfo'
            ])
        },
        methods: {
            ...mapActions([
                'GetProductDevListInfo'
            ]),
            autoAddTableInx,
            getDefaultTime,
            goToNewPage,

            // 列表信息查询
            handleQueryInfo(params = {}, isFn) {

                // 判断是否为功能参数
                if (isFn) {
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['product_dev_list']);
                }

                let obj = {
                    ...params
                };

                this.GetProductDevListInfo(obj).then(({ data }) => {

                    // 导出
                    if (data.status === 2) return;

                    this.handleQueryInfoCallBack(data);
                }).catch(err => {
                    console.log(err);
                });
            },

            // 查询重置
            handleResetQueryInfo() {
                this.queryData = {
                    productSPU: '',
                    supplierCode: '',
                    supplierList: '',
                    auditState: '',
                    rejectRemarks: '',
                    productName: '',
                    supplierName: '',
                    operateUser: '',
                    operateTime: '',
                }
            },

            //高级筛选
            handleClickFilter() {
                this.FilterShow = !this.FilterShow;
            },

            animation(isHide) {
                if (isHide) {
                    console.log(document.querySelector('body'));
                    document.querySelector('body').style.overflow = 'hidden';
                    return;
                }
                this.$refs.tableArea.handleRefreshTableHeight()
            },

            // 分页条数切换
            handleSizeChange(val) {
                this.handleQueryInfo({
                    limit: val,
                    offset: 1
                }, true)
            },

            // 跳转页数切换
            handleCurrentChange(val) {
                this.handleQueryInfo({
                    limit: this.pageData.currentList,
                    offset: val
                }, true)
            },

            // 保存勾选数据
            handleSaveCheckInfo(val) {
                this.saveCheckInfo = val;
            },

            // 导出明细信息
            handleExportListInfo() {
                this.handleQueryInfo({
                    isExcel: 1
                }, true);
            },

            // 预览图片
            handleLookImage(src) {
                this.isShowLookImage = true;
                this.lookImageUrl = ['http://pic10.nipic.com/20101016/2531170_233023137000_2.jpg'];
            },

            // 审核
            handleClickAudit() {
                if (!this.saveCheckInfo.length) return;
                this.$confirm('确认审核？', '提示', {
                    type: 'warning'
                }).then(() => {

                    // let ids = [], states = [];
                    // this.saveCheckInfo.forEach(value => {
                    //     ids.push(value.supplier_id);
                    //     states.push(value.status);
                    // });
                    //
                    // let obj = {
                    //     supplier_ids: ids.join(','),
                    //
                    //     // 2为待审核
                    //     sup_status: 2,
                    //     current_status: states.join(','),
                    // };
                    //
                    // console.log(obj);
                    // return;
                    //
                    // changeSupplierState(obj).then(({ data }) => {
                    //     this.handleOperationCallBack({status: 1});
                    // });
                })
            },

            // 驳回
            handleClickReject() {
                if (!this.saveCheckInfo.length) return;
                this.$confirm('确认驳回？', '提示', {
                    type: 'warning'
                }).then(() => {

                    // let ids = [], states = [];
                    // this.saveCheckInfo.forEach(value => {
                    //     ids.push(value.supplier_id);
                    //     states.push(value.status);
                    // });
                    //
                    // let obj = {
                    //     supplier_ids: ids.join(','),
                    //
                    //     // 2为待审核
                    //     sup_status: 2,
                    //     current_status: states.join(','),
                    // };
                    //
                    // console.log(obj);
                    // return;
                    //
                    // changeSupplierState(obj).then(({ data }) => {
                    //     this.handleOperationCallBack({status: 1});
                    // });
                })
            },

            // 操作回调
            handleOperationCallBack(data) {
                if (data.status) {
                    this.$message.success('操作成功');
                    this.handleQueryInfo({ limit: this.pageData.currentList });
                } else {
                    this.$message.error(data.errorMess || '操作失败');
                }
            },

            //查询回调
            handleQueryInfoCallBack(data){

                // 保存查询参数
                this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                    key: 'product_dev_list',
                    value: JSON.stringify(this.queryData)
                });

                // 渲染下拉

                //数据渲染
                this.tableData = data.data_list.value;
                this.tableKey = data.data_list.key;
                // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
                this.tableProps = ['index', 'supplier_code', 'supplier_name', 'fh_address_shi', 'ca_data_user', 'payment_data_type', 'operate_dev_user', 'file_url', 'state', 'reject_remarks'];
                this.tableWidth = ['60', '150', '250', '180', '150', '150', '150', '200', '80', 'reject_remarks'];

                //渲染分页
                this.pageData.currentList = data.page_data.limit * 1;
                this.pageData.currentPage = data.page_data.offset * 1;
                this.pageData.currentTotal = data.page_data.total * 1;
            }
        },
        created() {
            this.thisVm = this;
            try{
                if (JSON.stringify(this.productDevListInfo) === '{}') {
                    this.handleQueryInfo({is_get_base: 1});
                }else{
                    // 获取保存的查询参数
                    this.queryData = JSON.parse(this.queryParamsInfo['product_dev_list']);
                    this.handleQueryInfoCallBack(this.productDevListInfo);
                }
            }catch (e) {
                console.log(e)
            }
        },
    };
</script>

<style lang="less" scoped>

    .ui-table--supplier_code {
        font-size: 12px;
        line-height: 10px;
        p:nth-child(1) {
            font-weight: bold;
        }

        p {
            margin-bottom: 8px;
        }
    }

    /deep/ .el-table td, /deep/ .el-table th {
        overflow: hidden;
        text-align: left!important;
    }

    .ui-table--supplier_code--flag {
        width: 50px;
        height: 35px;
        line-height: 54px;
        text-align: center;
        -webkit-transform: rotateZ(45deg);
        transform: rotateZ(45deg);
        background: #63c4e9;
        position: absolute;
        font-size: 12px;
        color: #FFFFFF;
        top: -12px;
        right: -20px;
        font-weight: normal;
    }

    .ui-table--icon_btn {
        i {
            cursor: pointer;
        }
    }

    .ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
        li {
            width: 130px;
            /*height: 185px;*/
            margin-right: 20px;
            text-align: center;
            position: relative;

            img {
                width: 100%;
                height: 185px;
            }

            .ui-com_edit__upload--item--mask {

                display: none;
                position: absolute;
                top: 0;
                left: 0;
                width: 100%;
                height: 185px;
                background: rgba(0, 0, 0, .3);
                text-align: center;
                line-height: 185px;

                & > div {
                    display: inline-block;
                }

                span {
                    margin: 0 5px;
                    cursor: pointer;
                    color: #cccccc;
                    font-size: 20px;

                    &:hover {
                        color: #FFFFFF;
                    }
                }
            }

            &:hover {
                .ui-com_edit__upload--item--mask {
                    display: block;
                }
            }
        }
    }
</style>


