<template>
    <div class="ui-scan_page ui-main-module">
        <div class="ui-head">
            <ul class="ui-head-ope_item">
                <li>
                    <i class="icon-yonghu icon_blue"></i>
                    <el-input size="small" placeholder="请输入工号" @blur="handleUserInfo"
                              v-model="queryData.user_id"></el-input>
                    <el-input size="small" placeholder="带出部门" v-model="queryData.department"></el-input>
                    <el-input size="small" placeholder="带出姓名" v-model="queryData.handle_type">
                        <el-button slot="append">退出</el-button>
                    </el-input>
                </li>
                <li class="ui-head-ope_item--layout">
                    <el-button type="text" size="small" @click="isRelevance = !isRelevance">
                        <i v-show="!isRelevance" class="icon-erweimahong icon-danger"></i>
                        <i v-show="isRelevance" class="icon-erweimalan icon-main"></i>
                    </el-button>
                    <el-input size="small" @keyup.enter.native="handleScanInfo" placeholder="商品台车 / 精品SPU / 试卖SPU"
                              v-model="queryData.code">
                        <el-button slot="append">提交</el-button>
                    </el-input>
                </li>
            </ul>
        </div>
        <div class="ui-fn-module">
            <!--<span class="ui-top_i" @click="showSearch = !showSearch"><ComIcon :name="showSearch ? 'shrink_up' : 'shrink_down'"></ComIcon></span>-->
            <el-button type="text" size="small"><i class="icon-shanchusaoma icon-danger"></i>删除扫码</el-button>
            <el-button type="text" size="small" v-show="isRelevance"><i class="icon-querenguanlian icon-main"></i>确认关联
            </el-button>
            <el-button type="text" size="small" v-show="!isRelevance"><i class="icon-jiechubangding icon-warn"></i>解除绑定
            </el-button>
        </div>
        <div class="ui-main-module-table">
            <div class="ui-main-tab">
                <el-badge
                    v-for="(item, index) in moduleTypeList"
                    :key="index">
                    <li class="ui-main-tab-box">
                        <span @click="handleToggleTableInfo(item.value)"
                              :class="queryData.list_type === item.value && 'ui-active'">{{item.label}}<i class="ui-tab_line"></i></span>
                    </li>
                </el-badge>
            </div>
        </div>

        <div v-show="queryData.list_type==1">
            <ComTableInfo
                isCellCancel
                ref="boxSPURef"
                isCheck
                :tableKey="tableData.boxSPUKey"
                :tableProps="tableData.boxSPUProps"
                :tableData="tableData.boxSPUData"
                :tableWidth="tableData.tableWidth"
                @selectd="handleSelectAbout"
                @checked="handleSaveCheckInfo">

                <!--商品台车-->
                <template slot="num" slot-scope="scope">
                    <span>{{scope.row.num.num}}</span>
                </template>
                <template slot="pro_trolley" slot-scope="scope">
                    <span>{{scope.row.pro_trolley.trolley_code}}</span>
                </template>

                <template slot="pro_box" slot-scope="scope">
                    <span>{{scope.row.pro_box.box_code}}</span>
                </template>

                <template slot="pro_category" slot-scope="scope">
                    <span>一级目录</span>
                </template>

                <template slot="pro_amount" slot-scope="scope">
                    <p style="cursor: pointer;" @click="showDetail(scope.row.pro_box.box_id)">
                        {{scope.row.pro_amount.sku_count}}</p>
                </template>

                <template slot="send_sample_time" slot-scope="scope">
                    <p>开发部：{{scope.row.send_sample_time.modify_time}}</p>
                    <p>品控部：{{scope.row.send_sample_time.handle_time}}</p>
                </template>

                <template slot="send_sample_user" slot-scope="scope">
                    <p>开发部：{{scope.row.send_sample_user.modify_user}}</p>
                    <p>品控部：{{scope.row.send_sample_user.handle_user}}</p>
                </template>
                <template slot="remark" slot-scope="scope">
                    <p v-if="scope.row.remark.remark_user">用户名：{{scope.row.remark.remark_user}}
                        备注信息：{{scope.row.remark.remark}}</p>
                    <el-input type="small" v-model="scope.row.addRemark"></el-input>
                </template>


                <!--商品SPU-->
                <template slot="trolley_num" slot-scope="scope">
                    <span>{{scope.row.trolley_num.trolley_code}}</span>
                </template>

                <template slot="pro_spu" slot-scope="scope">
                    <span>{{scope.row.pro_spu.spu}}</span>
                </template>

                <template slot="pro_name" slot-scope="scope">
                    <span>{{scope.row.pro_name.category_name}}</span>
                </template>

                <template slot="bind_sku" slot-scope="scope">
                    <span>{{scope.row.bind_sku.sku}}</span>
                </template>
            </ComTableInfo>
        </div>

        <div v-show="queryData.list_type==2">
            <ComTableInfo
                isCellCancel
                ref="trolleyBoxRef"
                isCheck
                :tableKey="tableData.trolleyBoxKey"
                :tableProps="tableData.trolleyBoxProps"
                :tableData="tableData.trolleyBoxData"
                :tableWidth="tableData.tableWidth"
                @selectd="handleSelectAbout"
                @checked="handleSaveCheckInfo">

                <!--商品台车-->
                <template slot="num" slot-scope="scope">
                    <span>{{scope.row.num.num}}</span>
                </template>
                <template slot="pro_trolley" slot-scope="scope">
                    <span>{{scope.row.pro_trolley.trolley_code}}</span>
                </template>

                <template slot="pro_box" slot-scope="scope">
                    <span>{{scope.row.pro_box.box_code}}</span>
                </template>

                <template slot="pro_category" slot-scope="scope">
                    <span>一级目录</span>
                </template>

                <template slot="pro_amount" slot-scope="scope">
                    <p style="cursor: pointer;" @click="showDetail(scope.row.pro_box.box_id)">
                        {{scope.row.pro_amount.sku_count}}</p>
                </template>

                <template slot="send_sample_time" slot-scope="scope">
                    <p>开发部：{{scope.row.send_sample_time.modify_time}}</p>
                    <p>品控部：{{scope.row.send_sample_time.handle_time}}</p>
                </template>

                <template slot="send_sample_user" slot-scope="scope">
                    <p>开发部：{{scope.row.send_sample_user.modify_user}}</p>
                    <p>品控部：{{scope.row.send_sample_user.handle_user}}</p>
                </template>
                <template slot="remark" slot-scope="scope">
                    <p v-if="scope.row.remark.remark_user">用户名：{{scope.row.remark.remark_user}}
                        备注信息：{{scope.row.remark.remark}}</p>
                    <el-input type="small" v-model="scope.row.addRemark"></el-input>
                </template>


                <!--商品SPU-->
                <template slot="trolley_num" slot-scope="scope">
                    <span>{{scope.row.trolley_num.trolley_code}}</span>
                </template>

                <template slot="pro_spu" slot-scope="scope">
                    <span>{{scope.row.pro_spu.spu}}</span>
                </template>

                <template slot="pro_name" slot-scope="scope">
                    <span>{{scope.row.pro_name.category_name}}</span>
                </template>

                <template slot="bind_sku" slot-scope="scope">
                    <span>{{scope.row.bind_sku.sku}}</span>
                </template>
            </ComTableInfo>
        </div>
        <el-dialog title="查看台车明细" :visible.sync="dialogTableVisible">
            <ComTableInfo
                :tableKey="detailKey"
                :tableProps="detailProps"
                :tableData="detailData"
                :tableWidth="detailWidth">
                <template slot="num" slot-scope="scope">
                    {{scope.row.num.num}}
                </template>
                <template slot="pro_spu" slot-scope="scope">
                    {{scope.row.pro_spu.spu}}
                </template>
                <template slot="pro_name" slot-scope="scope">
                    {{scope.row.pro_name.product_name}}
                </template>

                <template slot="pro_category" slot-scope="scope">
                    {{scope.row.pro_category.one_category_name}}
                </template>

                <template slot="pro_amount" slot-scope="scope">
                    {{scope.row.pro_amount.sku_count}}
                </template>

                <template slot="bind_sku" slot-scope="scope">
                    {{scope.row.bind_sku.sku}}
                </template>
            </ComTableInfo>
        </el-dialog>
    </div>
</template>

<script>
import {
    mapGetters
} from 'vuex';
import {
    getProducScanListInfo,
    getSampleScanInfo,
    delSampleScan,
    sendSampleScan,
    getDetail
} from '@/api/dev_department/product_development/product_scan_relevance';

export default {
    name: 'ProductScanRelevance',
    data () {
        return {
            thisVm: {},
            dialogTableVisible: false,
            isRelevance: true, //是否关联
            queryData: {
                user_id: '',
                department: 2,
                handle_type: 1,
                list_type: 1
            },

            moduleTypeList: [
                {
                    label: '箱子&SPU',
                    value: 1
                },
                {
                    label: '台车&箱子',
                    value: 2
                },
                {
                    label: '台车&SPU',
                    value: 3
                }
            ],

            tableData: {
                //箱子&SPU
                boxSPUData: [],
                boxSPUKey: [],
                boxSPUProps: [],

                trolleyBoxData: [],
                trolleyBoxKey: [],
                trolleyBoxProps: [],

                trolleySPUData: [],
                trolleySPUKey: [],
                trolleySPUProps: [],

                tableWidth: [],
            },

            // 保存勾选数据
            saveCheckInfo: [],

            //查看台车明细
            detailData: [],
            detailKey: [],
            detailWidth: [],
            detailProps: [],

            testNum: 0
        };
    },
    computed: {
        ...mapGetters(['queryParamsInfo'])
    },
    methods: {
        //显示明细
        showDetail (id) {
            if (!id) {
                return;
            }

            this.dialogTableVisible = true;
            let obj = {
                box_id: id,
                ...this.queryData
            };
            getDetail(obj).then(({data}) => {
                console.log(data, 88);
                this.detailData = data.data.value;
                this.detailProps = Object.keys(data.data.key);
                this.detailKey = Object.values(data.data.key);
            });
        },

        //通过工号查询用户信息
        handleUserInfo () {
            console.log('aaa');
            this.$store.commit('SAVE_QUERY_PARAMS_INFO', {
                key: 'sample_scan',
                value: JSON.stringify(this.queryData)
            });

        },

        //选中时 同一台车的需要全部选中
        handleSelectAbout (obj) {
            this.$refs.trolleyBoxRef.$refs.myTable.toggleRowSelection(this.tableData.trolleyBoxData[1]);
            // this.tableData.boxSPUData.forEach((item, index) => {
            //     if (parseInt(this.queryData.list_type) === 1 && obj.data.pro_trolley.trolley_code) {
            //         if (obj.data.pro_trolley.trolley_code === item.pro_trolley.trolley_code && obj.data.num.num !== item.num.num) {
            //             obj.refObj.toggleRowSelection(this.tableData.boxSPUData[index]);
            //         }
            //     } else if (obj.data.trolley_num.trolley_code) {
            //         if (obj.data.trolley_num.trolley_code === item.trolley_num.trolley_code && obj.data.num.num !== item.num.num) {
            //             obj.refObj.toggleRowSelection(this.tableData.boxSPUData[index]);
            //         }
            //     }
            // });
        },

        // 保存勾选数据
        handleSaveCheckInfo (val) {
            this.saveCheckInfo = val;
        },

        //勾选的对象
        selectId () {
            let idArr = [];
            this.saveCheckInfo.forEach((obj) => {
                idArr.push(obj);
            });
            return idArr;
        },

        //删除扫码
        handleDelScan () {
            let arr = {
                trolley_id: [],
                spu: []
            };
            this.saveCheckInfo.forEach((item) => {

                //如果是商品台车
                if (this.queryData.list_type == 1) {
                    arr.trolley_id.push(item.pro_trolley.trolley_id);
                } else {

                    //如果是商品spu 有台车添加台车，否则添加 spu
                    if (item.trolley_num.trolley_code) {
                        arr.trolley_id.push(item.trolley_num.trolley_id);
                    } else {
                        arr.spu.push(item.pro_spu.spu);
                    }
                }
            });

            if (arr.trolley_id.length === 0 && arr.spu.length === 0) {
                this.$message.warning('请勾选数据！');
                return;
            }

            arr.trolley_id = Array.from(new Set(arr.trolley_id)).join(',');
            arr.spu = arr.spu.join(',');

            console.log(arr, 5656);

            this.$confirm('确认删除？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {
                    ...arr,
                };
                delSampleScan(obj).then(({data}) => {
                    if (data.status == 1) {
                        this.$message.success('删除成功');
                        this.handleQueryInfo();
                    }
                });
            });

        },
        //提交送样
        handleSendScan () {
            let arr = [];
            this.saveCheckInfo.forEach((item) => {

                //如果是商品台车
                if (this.queryData.list_type == 1) {
                    arr.push({
                        'box_id': item.pro_box.box_id,
                        'remark': item.addRemark
                    });
                } else {

                    //如果是商品spu 有台车添加台车，否则添加 spu
                    arr.push({
                        'spu': item.pro_spu.spu,
                        'remark': item.addRemark
                    });
                }
            });

            if (arr.length === 0) {
                this.$message.warning('请勾选数据！');
                return;
            }

            console.log(arr, 88);

            this.$confirm('确认提交？', '提示', {
                type: 'warning'
            }).then(() => {
                let obj = {};
                if (this.queryData.list_type == 1) {
                    obj.trolley_box = arr;
                } else {
                    obj.trolley_spu = arr;
                }
                console.log(typeof obj);

                sendSampleScan(obj).then(() => {

                    this.handleQueryInfo();
                });
            });

        },
        // 列表信息查询
        handleQueryInfo () {
            let obj = {
                ...this.queryData,
            };
            getProducScanListInfo(obj).then(({data}) => {
                this.handleQueryInfoCallBack(data);
            }).catch(err => {
                console.log(err);
            });
        },

        //扫码送样
        handleScanInfo () {
            let obj = {
                list_type: this.queryData.list_type,
                code: this.queryData.code,
            };
            getSampleScanInfo(obj).then(({data}) => {
                if (data.errorMess) {
                    this.$message({
                        message: data.errorMess,
                        type: 'warning'
                    });
                    return;
                }
                this.queryData.code = '';
                this.handleQueryInfo();
            });
        },

        //查询回调
        handleQueryInfoCallBack (data) {
            if (data.data) {

                //数据渲染
                this.tableData.boxSPUData = data.data.value;
                this.tableData.boxSPUData.forEach((item) => {
                    item.addRemark = '';
                });

                this.tableData.boxSPUProps = Object.keys(data.data.key);
                this.tableData.boxSPUKey = Object.values(data.data.key);

                this.tableData.trolleyBoxData = data.data.value;
                this.tableData.trolleyBoxData.forEach((item) => {
                    item.addRemark = '';
                });

                this.tableData.trolleyBoxProps = Object.keys(data.data.key);
                this.tableData.trolleyBoxKey = Object.values(data.data.key);
            } else {
                this.tableData.boxSPUData = [];
            }

            this.tableData.tableWidth[0] = '90px';

            if (parseInt(this.queryData.list_type) === 1) {

                this.tableData.tableWidth[4] = '90px';
                this.tableData.tableWidth[5] = '200px';
                this.tableData.tableWidth[7] = '300px';

            } else {
                this.tableData.tableWidth[5] = '90px';
                this.tableData.tableWidth[6] = '200px';
                this.tableData.tableWidth[9] = '300px';
            }

        },

        // 切换表格列表
        handleToggleTableInfo (val) {
            this.queryData.list_type = val;
        },
    },
    created () {
        this.handleQueryInfo();
        this.detailWidth[0] = '90px';
        this.detailWidth[4] = '90px';

        if (this.queryParamsInfo['sample_scan']) {
            this.queryData = JSON.parse(this.queryParamsInfo['sample_scan']);
        }
    }
};
</script>

<style scoped lang="less">
@border-color: #e6e6e6;
.ui-scan_page {
    text-align: left;

    .ui-head {
        padding: 20px;
        background: #FFFFFF;
        border: 1px solid @border-color;
    }

    .ui-head-ope_item {
        /deep/ .el-input {
            width: 200px;
        }
        li i {
            font-size: 22px;
            position: relative;
            top: 4px;
        }

        .ui-head-ope_item--layout {
            .el-input {
                width: 609px;
            }
        }
    }
}
</style>



