                                    <el-checkbox :indeterminate="syncIsNoAllCheck[6]" v-model="syncCheck[6]"
                                                 @change="handleSyncCheckAllChange(6, 'productComDatumSync')"></el-checkbox>
                                </div>
                                <ul class="clearFix">
                                    <li class="ui-layout">
                                        <span>质检等级</span>
                                        <el-form-item>
                                            <el-select>
                                                <el-option>1</el-option>
                                            </el-select>
                                        </el-form-item>
                                    </li>
                                    <li class="ui-layout">
                                        <span>抽样方案</span>
                                        <el-form-item>
                                            <el-input type="textarea" :rows="2"
                                                      resize="none"></el-input>
                                        </el-form-item>
                                    </li>
                                    <li class="ui-layout">
                                        <span>检测清单</span>
                                        <el-form-item>
                                            <div class="test-list">
                                                <ul class="parent-ul">
                                                    <li v-for="item of optionList.testList" :key="item">
                                                        <span>{{item.title}} >></span>
                                                        <ul class="child-ul" v-if="item.child">
                                                            <li v-for="child of item.child" :key="item">
                                                                {{child.txt}}
                                                            </li>
                                                        </ul>
                                                    </li>
                                                </ul>
                                            </div>
                                        </el-form-item>
                                        <el-form-item class="ui-ope_area clearFix">
                                            <div v-for="(item, index) in editData.sellingPoint"
                                                 class="ui-ope_area--item">
                                                <el-input v-model="editData.sellingPoint[index]"></el-input>
                                                <div class="ui-ope_area--btn">
                                                    <span class="icon-shanjian"
                                                          @click="handleDeleteInput('sellingPoint', index)"></span>
                                                    <span class="icon-zengjia"
                                                          @click="handleAddInput('sellingPoint', index + 1)"></span>
                                                </div>
                                            </div>
                                        </el-form-item>
                                    </li>
                                    <li class="ui-layout">
                                        <span>质量备注</span>
                                        <el-form-item>
                                            <el-input type="textarea" :rows="2"
                                                      resize="none"></el-input>
                                        </el-form-item>
                                    </li>
                                </ul>
                            </el-collapse-item>
                        </div>
                        <div class="ui-content_area--item"
                             :class="(syncIsNoAllCheck[7] || syncCheck[0]) && 'ui-sync_check'">
                            <el-collapse-item title="| 样品异常记录" name="8">
                                <div class="ui-checkbox">
                                    <el-checkbox :indeterminate="syncIsNoAllCheck[7]" v-model="syncCheck[7]"
                                                 @change="handleSyncCheckAllChange(7, 'productComDatumSync')"></el-checkbox>
                                </div>
                                <ul class="clearFix">
                                    <li class="ui-layout">
                                        <el-checkbox v-model="editData.exceptionRecordFlag">添加记录</el-checkbox>
                                        <el-table :data="editData.rejectData" v-if="editData.exceptionRecordFlag">
                                            <el-table-column label="退样类型" width="100">
                                                <template slot-scope="scope">
                                                    <el-select v-model="scope.row.type"
                                                               @change="rejectTypeChange(scope.row)">
                                                        <el-option v-for="item in scope.row.rejectList"
                                                                   :label="item.reject_type_text"
                                                                   :value="item.id"
                                                                   :key="item.id"></el-option>
                                                    </el-select>
                                                </template>
                                            </el-table-column>
                                            <el-table-column label="产品异常描述" width="120">
                                                <template slot-scope="scope">
                                                    <el-input type="textarea" :rows="4"
                                                              v-model="scope.row.setting_introduce"></el-input>
                                                </template>
                                            </el-table-column>
                                            <el-table-column label="开发解决方案" width="120">
                                                <template slot-scope="scope">
                                                    <p v-for="(item,index) in scope.row.reject_to_dpts" :key="index">
                                                        {{item}}
                                                    </p>
                                                </template>
                                            </el-table-column>
                                            <el-table-column label="操作人员" width="90">
                                                <template slot-scope="scope">
                                                    {{scope.row.remark}}
                                                </template>
                                            </el-table-column>
                                            <el-table-column label="不良图片">
                                                <template slot-scope="scope">
                                                    <div class="upload-img-list">
                                                        <div class="ui-com_edit__upload--item"
                                                             v-if="scope.row.image!==''">
                                                            <div class="ui-com_edit__upload--item__img"
                                                                 :style="{background:'url('+scope.row.image+') no-repeat 0/contain'}">
                                                            </div>
                                                            <div class="ui-com_edit__upload--item--mask">
                                    <span class="el-icon-delete"
                                          @click="scope.row.image=''"></span>
                                                                <span class="el-icon-zoom-in"
                                                                      @click="handleLookImage(scope.row.image)"></span>
                                                            </div>
                                                        </div>
                                                        <div class="ui-com_edit__upload--last_btn"
                                                             v-if="scope.row.image==''">
                                                            <el-upload
                                                                action=""
                                                                multiple
                                                                :show-file-list="false"
                                                                :auto-upload="false"
                                                                :on-change="(file) =>{handleSaveFileList(file,scope.row)}">
                                                                <span class="icon-tupianshangchuan"></span>
                                                            </el-upload>
                                                        </div>
                                                    </div>
                                                </template>
                                            </el-table-column>

                                            <el-table-column label="操作" width="100">
                                                <template slot-scope="scope">
                                                    <div class="ui-ope_area--btn">
                                                        <span class="icon-shanjian"
                                                              @click="handleDeleteInput(scope.$index)"></span>
                                                        <span class="icon-zengjia"
                                                              @click="handleAddInput(scope.$index)"></span>
                                                    </div>
                                                </template>
                                            </el-table-column>
                                        </el-table>
                                    </li>
                                </ul>
                            </el-collapse-item>
                        </div>
                        <div class="ui-content_area--btn">
                            <el-button size="mini" @click="handleSaveOrSubmitInfo('1')">保存</el-button>
                            <el-button type="primary" size="mini" @click="handleSaveOrSubmitInfo('2')">审核</el-button>
                            <el-button type="primary" size="mini" @click="handleSaveOrSubmitInfo('2')">驳回</el-button>
                        </div>
                    </el-collapse>
                </div>
            </el-form>
            <LookImage :src="lookImageUrl" :show.sync="isShowLookImage"></LookImage>
        </div>
        <div class="ui-right">
            <ul class="ui-right--item">
                <li>
                    <el-checkbox :indeterminate="isIndeterminate" v-model="checkAllSku" @change="handleCheckAllChange">
                        All
                    </el-checkbox>
                    <el-button type="primary" size="mini" @click="handleSyncSkuInfo">同步</el-button>
                </li>
                <li>
                    <el-checkbox-group v-model="checkSku" @change="handleRelevanceSkuChange">
                        <el-checkbox v-for="item in skuList" :label="item" :key="item">{{item}}</el-checkbox>
                    </el-checkbox-group>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import {
    getRepairOptionListInfo,
    submitSaveSKURepairInfo,
    syncSkuInfo,
    getSPURepairInfo
} from '@/api/dev_department/product_development/product_datum_create';
import {
    imageUrlChangeFile
} from '@/services';
import CONFIG from '@/assets/js/config';

export default {
    name: 'SKUPage',
    props: ['row', 'sku'],
    data () {
        return {
            testCheck: [],

            editShowArea: ['1', '2', '3', '4', '5', '6'],

            tableKey: ['商品SKU', '商品名称', '产品目录', '样品类型', '供应商资料'],
            tableProps: ['sku', 'good_name', 'category_path', 'sample_type', 'good_info'],

            optionList: {
                packTypeList: {},
                brandAttrList: {},
                attrCheckList: {},
                countryList: {},
                infringementType: {},
                platformList: {},
                testList: [
                    {
                        title: 'aa',
                        txt: 'aa-txt',
                        child: [
                            {
                                title: 'aa-child-1',
                                txt: 'aa-child-txt-1'
                            },
                            {
                                title: 'aa-child-2',
                                txt: 'aa-child-txt-2'
                            }
                        ]
                    },
                    {
                        title: 'bb',
                        txt: 'bb-txt'
                    },
                    {
                        title: 'cc',
                        txt: 'cc-txt'
                    }
                ]
            },

            editData: {
                sellingPoint: ['', '', ''],
                exceptionRecordFlag: false,
                rejectData: [],
                packType: '',
                packWeight: '',
                packLength: '',
                sampleWeight: '',
                sampleRemarks: '',
                packList: '',
                brandAttr: '',
                productBrand: '',
                productModel: '',
                isHaveLogo: '',
                materialQuality: '',
                productUse: '',
                workPrinciple: '',
                isTort: '',
                isCheckPrice: '',
                isEmpower: '',
                isDisSales: '',
                checkPriceInfo: [[
                    {}, {}, {}
                ]],
                tortInfo: [[
                    {}, {}
                ]],
                disSalesInfo: [[
                    {}, {}
                ]],
                empowerInfo: {},
                publishAttr: [],
                orderAttr: [],
                orderAttrFile: {},
                basisAttr: [],
                specialAttr: [],
                batteryAttr: [],
                batteryInfo: [],
                isBanAttr: [],
                banInfo: '',
                modelAttr: [],
                overseas: [],
                certificateType: [],
                certificateTypeInfo: '',
                instructions: [],
                instructionsInfo: ''
            },
            editRule: {},

            // 显示属性隐藏字段
            showAttrHideKey: {
                batteryAttr: ['内置电池', '配套电池', '纯电池'],
                isBanAttr: ['管制刀具'],
                certificateType: ['其它'],
                instructions: ['其它'],
                orderAttr: ['海外直发']
            },

            // 保存文件列表
            saveFileData: {
                appendixDatum: [],
                devImage: [],
            },
            saveUrlFile: {
                devImage: [],
                appendixDatum: [],
            },

            // 预览图片
            lookImageUrl: '',
            isShowLookImage: false,

            // 关联SKU
            isIndeterminate: false,
            checkAllSku: false,
            checkSku: [],

            // 同步信息
            syncLabelCheck: {
                appendixDatumSync: [],
                devImageSync: [],
                productComDatumSync: [],
                tortDatumSync: [],
                productAttrDatumSync: []
            },
            syncCheck: [],
            syncIsNoAllCheck: [],
            syncCheckArea: [
                ['packType', 'packWeight', 'packLength', 'sampleWeight'],
                ['brandAttr', 'productBrand', 'productModel', 'isHaveLogo', 'materialQuality', 'productUse', 'workPrinciple', 'isTort', 'isCheckPrice', 'isEmpower', 'isDisSales', 'checkPriceInfo', 'tortInfo', 'disSalesInfo'],
                null, null,
                ['publishAttr', 'orderAttr', 'basisAttr', 'specialAttr', 'batteryAttr', 'isBanAttr', 'modelAttr', 'overseas', 'certificateType', 'instructions']
            ]
        };
    },
    computed: {
        tableData () {
            if (JSON.stringify(this.row) === '{}') {
                return [];
            } else {
                return [{
                    ...this.row,
                    sku: this.sku
                }];
            }
        },
        skuList () {
            let skuList = [];
            if (this.$parent.row && this.$parent.row.sku) {
                skuList = this.$parent.row.sku.split(',');
            }
            return skuList;
        },
        firstUrl () {
            return CONFIG.PRODUCTION_PATH;
        },
    },
    created () {
        getRepairOptionListInfo().then(({data}) => {
            this.optionList.attrCheckList = data.select_list.attribute;
            this.optionList.packTypeList = data.select_list.pack_type;
            this.optionList.brandAttrList = data.select_list.brand_attribute;
            this.optionList.countryList = data.select_list.country;
            this.optionList.infringementType = data.select_list.infringement_type;
            this.optionList.platformList = data.select_list.platform;
            if (Array.isArray(this.$parent.editPageData[this.sku])) {
                let obj = {
                    is_reject: this.row.reject_type,
                    spu: this.row.spu[0]
                };
                getSPURepairInfo(obj).then(({data}) => {
                    data.data.myfile.forEach((value, index) => {
                        this.saveFileData.devImage.push({
                            url: value
                        });

                        let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                        imageUrlChangeFile(`${this.firstUrl}/${value}`, fileName[0].replace('.', ''), (file) => {
                            this.saveUrlFile.devImage.splice(index, 1, file);
                        });
                    });
                    this.editData.sampleRemarks = data.data.buy_sample_note;
                    let isNotPack = true;
                    data.data.otherSku && JSON.parse(data.data.otherSku).forEach(value => {
                        if (value.sku === this.sku) {
                            this.editData.packList = value.packList;
                            isNotPack = false;
                        }
                    });
                    if (isNotPack) {
                        this.editData.packList = data.data.pack_list;
                    }
                });
            } else {
                this.handleInitData();
            }
        });
    },
    methods: {
        //获取驳回原因
        handleRejectInfo () {

            getRejectInfo({reject_from_dpt: 3}).then(({data}) => {
                this.rejectList = data.data.list;
                this.rejectDefault = {
                    rejectList: this.rejectList,
                    type: '',
                    setting_introduce: '',
                    reject_to_dpt: '',
                    reject_to_dpts: [],
                    remark: '',
                    image: ''
                };
                this.editData.rejectData.push(Object.assign({}, this.rejectDefault));
            });
        },

        // 保存文件列表
        handleSaveFileList (type, fileList) {
            this.saveFileData[type] = [...this.saveFileData[type], ...fileList];
            this.$refs[type].clearFiles();
        },

        // 预览图片
        handleLookImage (url) {
            this.isShowLookImage = true;
            this.lookImageUrl = url;
        },

        // 删除文件
        handleDeleteFile (inx, key) {
            this.saveFileData[key].splice(inx, 1);
            this.saveUrlFile[key].splice(inx, 1);
        },

        // 添加input
        handleAddInput (key, index) {
            let pushVal;
            switch (key) {
                case 'disSalesInfo':
                    pushVal = [{}, {}];
                    break;
                case 'tortInfo':
                    pushVal = [{}, {}];
                    break;
                case 'checkPriceInfo':
                    pushVal = [{}, {}, {}];
                    break;
                default:
                    pushVal = '';
            }
            this.editData[key].splice(index, 0, pushVal);
        },

        // 删除input
        handleDeleteInput (key, index) {
            this.editData[key].splice(index, 1);
        },

        // 添加选中样式
        autoAddActiveClass (checkArr, inx) {
            return checkArr[inx];
        },

        /**
         * 显示隐藏属性K
         * @option { array or object } 下拉列表
         * @key { string } 主要key
         * @inx { number } 隐藏索引
         * @prop { object } 下拉键值
         * */
        autoShowHideAttrKey (option, key, inx, prop = {label: 'attribute_value_cn', value: 'id'}) {
            if (!option) return false;
            if (Array.isArray(option)) {
                for (let value of option) {
                    try {
                        if (this.editData[key].indexOf(value[prop.value]) > -1) {
                            let keyInx = this.showAttrHideKey[key].indexOf(value[prop.label]);
                            if (keyInx > -1 && keyInx === inx) {
                                return true;
                            }
                        }
                    } catch (e) {
                        console.log(this.editData[key]);
                        console.log(key);
                        console.log(prop.value);
                    }
                }
                return false;
            }
        },

        /**
         * label标签勾选
         * @key { string } 选择对象
         * @inx { number } 对象索引
         * @area { number } 对象区域
         * */
        handleSyncCheckLabel (key, inx, area) {
            this.$set(this.syncLabelCheck[key], inx, !this.syncLabelCheck[key][inx]);
            this.handleSyncCheckChange(area, key);
        },

        /**
         * 同步勾选
         * @area { number } 勾选区域
         * @index { number } 索引
         * @type { string } 对象
         * */
        handleSyncCheckChange (area, type) {
            let isAll = true;
            let isNoCheck = true;

            if (this.saveFileData[type.replace('Sync', '')]) {
                for (let i = 0; i < this.saveFileData[type.replace('Sync', '')].length; i++) {
                    if (!this.syncLabelCheck[type][i]) {
                        isAll = false;
                    } else {
                        isNoCheck = false;
                    }
                }
            } else {
                for (let i = 0; i < this.syncCheckArea[area].length; i++) {
                    if (!this.syncLabelCheck[type][i]) {
                        isAll = false;
                    } else {
                        isNoCheck = false;
                    }
                }
            }

            if (isAll) {
                this.syncCheck[area] = true;
                this.syncIsNoAllCheck[area] = false;
            } else if (isNoCheck) {
                this.syncCheck[area] = false;
                this.syncIsNoAllCheck[area] = false;
            } else {
                this.syncIsNoAllCheck[area] = true;
            }
        },

        // 同步全选
        handleSyncCheckAllChange (inx, type) {
            if (this.saveFileData[type.replace('Sync', '')]) {
                if (this.syncCheck[inx]) {
                    for (let i = 0; i < this.saveFileData[type.replace('Sync', '')].length; i++) {
                        this.syncLabelCheck[type][i] = true;
                    }
                    this.syncIsNoAllCheck[inx] = false;
                } else {
                    for (let i = 0; i < this.saveFileData[type.replace('Sync', '')].length; i++) {
                        this.syncLabelCheck[type][i] = false;
                    }
                    this.syncIsNoAllCheck[inx] = false;
                }
            } else {
                if (this.syncCheck[inx]) {
                    for (let i = 0; i < this.syncCheckArea[inx].length; i++) {
                        this.syncLabelCheck[type][i] = true;
                    }
                    this.syncIsNoAllCheck[inx] = false;
                } else {
                    for (let i = 0; i < this.syncCheckArea[inx].length; i++) {
                        this.syncLabelCheck[type][i] = false;
                    }
                    this.syncIsNoAllCheck[inx] = false;
                }
            }
        },

        // 保存或提交信息
        handleSaveOrSubmitInfo (type, editData = this.editData) {
            let saveFileData;
            if (type === '3') {
                saveFileData = editData;
            } else {
                saveFileData = this.saveFileData;
            }
            let obj = {
                pack_type: editData.packType,
                pack_weight: editData.packWeight,
                size: editData.packLength,
                weight: editData.sampleWeight,
                sample_remarks: editData.sampleRemarks,
                packing_list: editData.packList,
                brand_attribute: editData.brandAttr,
                brand: editData.productBrand,
                model: editData.productModel,
                logo: editData.isHaveLogo,
                material: editData.materialQuality,
                product_use: editData.productUse,
                working: editData.workPrinciple,
                authorized: editData.isEmpower,
                publish: editData.publishAttr,
                order_attribute: editData.orderAttr,
                basic: editData.basisAttr,
                special: editData.specialAttr,
                battery: editData.batteryAttr,
                prohibited: editData.isBanAttr,
                shape_attribute: editData.modelAttr,
                overseas_attribute: editData.overseas,
                certificate_type: editData.certificateType,
                instructions: editData.instructions,
                status: type,
                battery_type: editData.batteryInfo && editData.batteryInfo[0],
                battery_model: editData.batteryInfo && editData.batteryInfo[1],
                battery_ml: editData.batteryInfo && editData.batteryInfo[2],
                certificate_other: editData.certificateTypeInfo,
                instructions_other: editData.instructionsInfo,
            };

            const radioDataChange = (rkey, rinfo, oinfo) => {
                if (editData[rkey] === '1') {
                    let objArr = [];
                    this.editData[rinfo].forEach(value => {
                        objArr = [...objArr, ...value];
                    });

                    let infoArr = [];
                    objArr.forEach(value => {
                        let arr = [];
                        for (let key in value) {
                            arr.push(value[key]);
                        }
                        infoArr.push(arr);
                    });
                    obj[oinfo] = JSON.stringify([...objArr]);
                } else {
                    obj[oinfo] = '0';
                }
            };

            radioDataChange('isCheckPrice', 'checkPriceInfo', 'limit_price');
            radioDataChange('isTort', 'tortInfo', 'infringement');
            radioDataChange('isDisSales', 'disSalesInfo', 'is_prohibited');

            let formData = new FormData();

            for (let key in obj) {
                if (obj[key] || obj[key] === 0) {
                    formData.append(key, obj[key] || '');
                }
            }

            let isBigSubmit = type === '2' && this.$parent.submitSkuList.indexOf(this.sku) === -1 &&
                (this.$parent.submitSkuList.length === this.skuList.length - 1);

            if (type === '3') {
                this.checkSku.forEach((v, i) => {
                    formData.append(`sku[${i}]`, v);
                });
            } else if (isBigSubmit) {
                let obj = {
                    sku: this.skuList,
                    spu: this.row.spu[0]
                };
                formData.append('sku', this.sku);
                formData.append('big_status', JSON.stringify(obj));
            } else {
                formData.append('sku', this.sku);
            }

            // file
            saveFileData.appendixDatum.forEach((value, index) => {
                console.log(this.saveUrlFile.appendixDatum);
                if (value.raw) {
                    formData.append(`file[${index}]`, value.raw);
                } else {
                    formData.append(`file[${index}]`, this.saveUrlFile.appendixDatum[index]);
                }
            });
            saveFileData.devImage.forEach((value, index) => {
                if (value.raw) {
                    formData.append(`myfile[${index}]`, value.raw);
                } else {
                    formData.append(`myfile[${index}]`, this.saveUrlFile.devImage[index]);
                }
            });

            if (editData.isEmpower && editData.isEmpower === '1') {
                formData.append('authorized_file', editData.empowerInfo.raw);
            }

            if (editData.orderAttrFile && editData.orderAttrFile !== '{}') {
                formData.append('research_file', editData.orderAttrFile.raw);
            }

            if (type === '3') {
                syncSkuInfo(formData).then(({data}) => {
                    console.log(data);
                });
            } else {
                submitSaveSKURepairInfo(formData).then(({data}) => {
                    this.handleOperationCallBack(data);
                });
            }
        },

        // 关联SKU
        handleRelevanceSkuChange (value) {
            let checkedCount = value.length;
            this.checkAllSku = checkedCount === this.skuList.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.skuList.length;
        },

        // 全选SKU
        handleCheckAllChange (val) {
            this.checkSku = val ? this.skuList : [];
            this.isIndeterminate = false;
        },

        // 同步SKU信息
        handleSyncSkuInfo () {

            // 因为一开始使用索引后面改键值对导致这里要用数组索引查找键，数组对应的索引匹配syncLabelCheck对象的键
            const keyObj = ['productComDatumSync', 'tortDatumSync', 'appendixDatumSync', 'devImageSync', 'productAttrDatumSync'];
            let keyArr = [];
            let fileObj = {
                appendixDatum: [],
                devImage: []
            };
            this.syncIsNoAllCheck.forEach((value, index) => {
                if (value || this.syncCheck[index]) {
                    this.syncLabelCheck[keyObj[index]].forEach((v, i) => {
                        if (v) {
                            // 对区域2跟3做文件操作处理
                            if (index === 2 || index === 3) {
                                let fileKey = keyObj[index].replace('Sync', '');
                                fileObj[keyObj[index].replace('Sync', '')].push(this.saveFileData[fileKey][i]);
                            } else {
                                keyArr.push(this.syncCheckArea[index][i]);
                            }
                        }
                    });
                }
            });

            let obj = {...fileObj};
            keyArr.forEach(key => {
                obj[key] = this.editData[key];
            });

            // console.log(obj);
            this.handleSaveOrSubmitInfo('3', obj);
        },

        // 初始化数据
        handleInitData () {

            let data = this.$parent.editPageData[this.sku];
            const attrCheckFn = (eKey, iKey) => {
                if (data[iKey]) {
                    this.editData[eKey] = data[iKey].split(',');
                }
            };

            const multipleAreaFn = (eKey, iKey, area) => {
                let iArr = data[iKey] && JSON.parse(data[iKey]);
                if (!iArr && iArr !== 0) {
                    this.editData[area] = '';
                    return;
                } else {
                    this.editData[area] = iArr ? '1' : '2';
                }
                if (area === 'isCheckPrice') {
                    Array.isArray(iArr) && iArr.forEach((value, index) => {
                        let keyInx = (index + 1) / 3 - 1;
                        if (Array.isArray(this.editData[eKey][Math.ceil(keyInx)])) {
                            this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 3, value);
                        } else {
                            this.$set(this.editData[eKey], Math.ceil(keyInx), []);
                            this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 3, value);
                        }
                    });
                } else {
                    Array.isArray(iArr) && iArr.forEach((value, index) => {
                        let keyInx = (index + 1) / 2 - 1;
                        if (Array.isArray(this.editData[eKey][Math.ceil(keyInx)])) {
                            this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 2, value);
                        } else {
                            this.$set(this.editData[eKey], Math.ceil(keyInx), []);
                            this.$set(this.editData[eKey][Math.ceil(keyInx)], index % 2, value);
                        }
                    });
                }
            };

            this.editData.packType = data.pack_type;
            this.editData.packWeight = data.pack_weight;
            this.editData.packLength = data.size;
            this.editData.sampleWeight = data.weight;
            this.editData.sampleRemarks = data.sample_remarks;
            this.editData.packList = data.packing_list;
            this.editData.brandAttr = data.brand_attribute;
            this.editData.productBrand = data.brand;
            this.editData.productModel = data.model;
            this.editData.isHaveLogo = data.logo;
            this.editData.materialQuality = data.material;
            this.editData.productUse = data.product_use;
            this.editData.workPrinciple = data.working;
            this.editData.isEmpower = data.authorized;
            this.editData.empowerInfo = {};
            this.editData.orderAttrFile = {};
            this.editData.batteryInfo = [data.battery_type, data.battery_model, data.battery_ml];
            // this.editData.banInfo = data.prohibited;
            this.editData.certificateTypeInfo = data.certificate_other;
            this.editData.instructionsInfo = data.instructions_other;

            attrCheckFn('publishAttr', 'publish');
            attrCheckFn('orderAttr', 'order_attribute');
            attrCheckFn('basisAttr', 'basic');
            attrCheckFn('specialAttr', 'special');
            attrCheckFn('batteryAttr', 'battery');
            attrCheckFn('isBanAttr', 'prohibited');
            attrCheckFn('modelAttr', 'shape_attribute');
            attrCheckFn('overseas', 'overseas_attribute');
            attrCheckFn('certificateType', 'certificate_type');
            attrCheckFn('instructions', 'instructions');

            multipleAreaFn('checkPriceInfo', 'limit_price', 'isCheckPrice');
            multipleAreaFn('tortInfo', 'infringement', 'isTort');
            multipleAreaFn('disSalesInfo', 'is_prohibited', 'isDisSales');

            data.myfile && data.myfile.forEach((value, index) => {
                let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                imageUrlChangeFile(`${this.firstUrl}/${value}`, fileName[0].replace('/', ''), (file) => {
                    this.saveUrlFile.devImage.splice(index, 1, file);
                    this.saveFileData.devImage.push({
                        url: value
                    });
                });
            });

            data.file && data.file.forEach((value, index) => {
                let fileName = /\/[0-9A-Za-z._-]+$/.exec(value) || ['未知图片.jpeg'];
                imageUrlChangeFile(`${this.firstUrl}/${value}`, fileName[0].replace('.', ''), (file) => {
                    this.$set(this.saveUrlFile.appendixDatum, index, file);
                    this.saveFileData.appendixDatum.push({
                        url: value
                    });
                    console.log(this.saveFileData.appendixDatum);
                });
            });
        },

        // 操作回调
        handleOperationCallBack (data) {
            console.log(data);
            if (data.status) {
                console.log(this.sku);
                this.$emit('submit', this.sku);
                this.$message.success('操作成功');
            } else {
                this.$message.error('操作失败');
            }
        }
    },
    watch: {
        sku (val) {
            this.handleInitData();
        }
    }
};
</script>

<style scoped lang="less">

@border-color: #e6e6e6;
@active-color: #3388ff;

@upload-height: 130px;

.ui-content_area {
    height: 100%;
    padding: 45px 150px 0;
    position: absolute;
    top: 0;
    width: 100%;
    box-sizing: border-box;

    .el-form {
        height: 100%;
    }

    .el-select {
        width: 100%;
    }

    .ui-active {
        color: @active-color !important;
    }

    .ui-sync_check {
        /deep/ .el-collapse-item__content {
            border-color: @active-color !important;
        }
    }

    .ui-content_area--item {
        .test-list {
            .parent-ul {
                > li {
                    float: left;
                    width: auto;
                    padding: 5px;
                    background-color: steelblue;
                    margin: 10px;
                    color: white;
                    border-radius: 3px;
                    position: relative;
                }

                .child-ul {
                    position: absolute;
                    left: -10px;
                    bottom: -60px;
                    min-width: 650px;
                    border: 1px solid green;

                    li {
                        background-color: orange;
                        float: left;
                        padding: 5px;
                        margin: 10px;
                        border-radius: 3px;
                    }
                }
            }
        }

        .ui-file__checkbox {
            position: absolute;
            top: 0;
            right: 5px;
        }

        .ui-checkbox {
            text-align: right;

            .el-checkbox {
                margin: 0;
            }

            /*& > span {*/
            /*position: absolute;*/
            /*top: 0;*/
            /*right: 0;*/
            /*font-size: 14px;*/
            /*}*/
        }

        &:not(:first-child) {
            margin-top: 20px;
        }

        ul.ui-content_area--list > li {
            /*line-height: 40px;*/
            /*height: 40px;*/
            position: relative;
            padding-top: 10px;
            min-height: 24px;
        }

        .ui-label {
            padding-left: 10px;
            padding-top: 10px;
            /*height: 40px;*/
            /*line-height: 40px;*/
            width: 100px;
            font-size: 14px;
            color: #666666;
            position: absolute;
            top: 0;
            left: 0;
            cursor: default;
        }

        .ui-value {
            margin-left: 110px;

            span {
                font-size: 14px;
                color: #666666;
            }
        }

        .ui-check__input {
            width: 100%;

            & > div {
                float: left;
                margin-right: 2px;
            }
        }
    }

    .ui-content_area--left {
        float: left;
        width: 55%;
        padding-top: 10px;
        border-right: 1px solid @border-color;
        box-sizing: border-box;
        padding-right: 10px;
        height: 100%;
        overflow: auto;
    }

    .ui-content_area--right {
        float: right;
        width: 45%;
        padding-top: 10px;
        padding-left: 10px;
        box-sizing: border-box;
        max-height: 100%;
        overflow: auto;
    }

    .ui-ope_area {
        position: relative;
        margin-top: 50px;

        .el-input {
            margin-right: 70px;
            width: unset;
            display: block;
        }

        .ui-ope_area--btn {
            position: absolute;
            right: 0;
            top: 0;

            span {
                cursor: pointer;
            }
        }

        .ui-ope_area--item {
            position: relative;
            margin-bottom: 5px;

            &:last-child {
                margin-bottom: 0;
            }
        }

        .ui-select--layout {
            /deep/ .el-select .el-input {
                width: 100px;
            }

            /deep/ .el-input-group__prepend {
                background-color: #fff;
            }

            /*.el-input {*/
            /*width: unset;*/
            /*}*/
        }
    }

    .ui-content_area--form {
        margin-top: 10px;

        li:not(.ui-layout) {
            &:nth-child(2n) {
                float: right;
            }
        }

        li {
            float: left;
            width: 40%;

            /*&:not(.ui-layout) {*/
            /*&*/
            /*}*/

            &.ui-layout {
                & > span {
                    font-size: 14px;
                    color: #606266;
                    padding-left: 4px;
                }

                width: 100%;
            }

            &.ui-ope_area {
                position: relative;

                .el-input {
                    margin-right: 70px;
                    width: unset;
                    display: block;
                }

                .ui-ope_area--btn {
                    position: absolute;
                    right: 0;
                    top: 0;

                    span {
                        cursor: pointer;
                    }
                }

                .ui-ope_area--item {
                    position: relative;
                    margin-bottom: 5px;

                    &:last-child {
                        margin-bottom: 0;
                    }
                }

                .ui-select--layout {
                    /deep/ .el-select .el-input {
                        width: 100px;
                    }

                    /deep/ .el-input-group__prepend {
                        background-color: #fff;
                    }

                    /*.el-input {*/
                    /*width: unset;*/
                    /*}*/
                }
            }

            &.ui-ope_area--three {
                .el-input {
                    width: 30%;
                    display: inline-table;
                    margin-right: 2px;
                }
            }

            &.ui-ope_area--tow {
                .el-input, .ui-ope__input {
                    width: 45%;
                    display: inline-table;
                    margin-right: 10px;
                }

                .ui-ope__input {
                    float: left;
                    display: flex;

                    .el-input {
                        width: 45%;
                        float: left;
                    }

                    .el-select {
                        width: 30%;
                        float: left;
                    }
                }

                .ui-select--layout {
                    .ui-layout__select {
                        margin-left: 6px;
                        width: 96%;

                        /deep/ .el-input {
                            width: 96%;
                        }
                    }
                }
            }
        }
    }

    .ui-content_area--btn {
        text-align: center;
        padding: 20px 0;
    }
}

.ui-com_edit__upload {
    .ui-com_edit__upload--title {
        font-size: 14px;
        font-weight: bold;
        text-align: left;
