            }
        }
    }
}

.ui-right {
    right: 0;
    top: 0;
    width: 140px;
    position: absolute;
    height: 100%;
    border-left: 1px solid @border-color;
    padding-top: 45px;
    box-sizing: border-box;
    overflow: hidden;

    .ui-right--item {
        padding: 10px;
        box-sizing: border-box;

        .el-button {
            /*margin-left: 20px;*/
            height: 20px;
            line-height: 20px;
            width: 50px;
            text-align: center;
            padding: 0;
        }
    }

    .el-checkbox {
        margin-bottom: 5px;
    }


}
</style>



