        sample_purchase,
        sample_track_manage,
        sample_track_record,
        copy_edit_list,
        copy_edit_record,
        audit_record,
        sample_abnormal_record,
        sample_audit_list,
        photography_goods_list,
        goods_modification_list,
        copy_group_manage,
        photography_group_manage,
        atr_group_manage,
        sample_group_manage,
        art_picture_upload
