<template>
    <TemplateDefault color="#fff">
        <div class="ui-batch_create">
            <div class="ui-batch_create--fn">
                <ComNewUpload></ComNewUpload>
                <!--<ComUpload label="选择文件" url="url"></ComUpload>-->
            </div>
            <ComTableInfo
                    :tableKey="tableKey"
                    :tableData="tableData"
                    :tableProp="tableProp"
                    :otherHeight="['ui-batch_create--fn']"></ComTableInfo>
        </div>
    </TemplateDefault>
</template>

<script>
    export default {
        name: "BatchCreatePage",
        data() {
            return {
                tableKey: [],
                tableData: [],
                tableProp: [],
            }
        }
    }
</script>

<style scoped lang="less">
    .ui-batch_create {
        padding: 0 20px;
        .ui-batch_create--fn {
            text-align: left;
            margin: 20px 0;
        }
    }
</style>


