      assignType: 1, // 1 指派， 2 重新指派；
      //高级筛选
      filterShow: false,

      // 时间搜索框宽度设置
      searchDateWidth: config.SEARCH_DATE_WIDTH,

      thisVm: {},

      // 搜索缩进
      showSearch: true,

      queryData: {
        moduleType: "1", //1 待分配  2 待编辑  3 已编辑
        productSPU: "",
        supplierCode: "",
        productList: [],
        workUser: "",
        functionInfo: "",
        auditState: "",
        operateUseTime: "",
        productName: "",
        supplierName: "",
        operateUser: "",
        operateTime: []
      },

      optionList: {
        productList: [],
        functionInfoList: {},
        statusList: {},
        moduleTypeList: [
          {
            label: "待分配",
            value: "1"
          },
          {
            label: "待编辑",
            value: "2"
          }
        ],
        workerList: {
          "1": "张三",
          "2": "李四"
        }
      },

      tableData: [],
      tableKey: [],
      tableWidth: [],
      tableProps: [],

      // 保存勾选数据
      saveCheckInfo: [],

      //分页
      pageSizes: config.pageData.pageSizes,
      pageData: {
        currentPage: 1,
        currentList: 20,
        currentTotal: 0
      },

      // 编辑
      isEditShow: false,
      formLabelWidth: "110px",
      saveCheckId: "",
      editData: {
        comCode: "",
        supplierName: "",
        applyAddress: "",
        applyDate: "",
        companyType: "",
        supplierLink: "",
        pAndCity: [],
        area: "",
        contacts: "",
        phoneNumber: "",
        contactsPhone: "",
        wxNumber: "",
        qqNumber: "",
        email: "",
        paymentType: "",
        settlementType: "",
        point: ""
      },
      editRule: {},

      addFormItemConfig: {
        "1": [
          {
            label: "店铺名称",
            key: "shopName",
            value: ""
          },
          {
            label: "收款名称",
            key: "gatheringName"
          },
          {
            label: "收款账号",
            key: "gatheringAccount"
          },
          {
            label: "开发人",
            key: "devUser"
          },
          {
            label: "审核人",
            key: "auditUser"
          },
          {
            label: "导入日期",
            key: "importDate"
          }
        ],
        "2": [
          {
            label: "收款人手机号",
            key: "gatheringPhone",
            isLabelLayout: true
          },
          {
            label: "收款名称",
            key: "gatheringName"
          },
          {
            label: "收款账号",
            key: "gatheringAccount"
          },
          {
            label: "收款人身份证",
            key: "gatheringId",
            isLabelLayout: true
          },
          {
            label: "开发人",
            key: "devUser"
          },
          {
            label: "审核人",
            key: "auditUser"
          },
          {
            label: "导入日期",
            key: "importDate"
          }
        ],
        "3": [
          {
            label: "开户行名称",
            key: "bankName"
          },
          {
            label: "收款名称",
            key: "gatheringName"
          },
          {
            label: "收款账号",
            key: "gatheringAccount"
          },
          {
            label: "开发人",
            key: "devUser"
          },
          {
            label: "审核人",
            key: "auditUser"
          },
          {
            label: "导入日期",
            key: "importDate"
          }
        ]
      },

      // 预览图片
      isShowLookImage: false,
      lookImageUrl: [],

      // 上传列表
      saveFileList: [],

      //文案小组和商品分类
      classSearch: [
        {
          title: "全部",
          number: 400
        },
        {
          title: "手机周边",
          number: 35
        },
        {
          title: "全部",
          number: 350
        },
        {
          title: "服饰珠宝",
          number: 50000
        },
        {
          title: "服饰珠宝",
          number: 5
        },
        {
          title: "服饰珠宝",
          number: 5012121
        },
        {
          title: "服饰珠宝",
          number: 50121212
        },
        {
          title: "服饰珠宝",
          number: 5012121
        },
        {
          title: "服饰珠宝",
          number: 501212
        },
        {
          title: "服饰珠宝",
          number: 501212
        },
        {
          title: "服饰珠宝",
          number: 501213213
        }
      ],

      //指派任务弹窗
      isShowAssign: false,

      //驳回弹窗
      isShowReject: false,

      isShowEdit: false
    };
  },
  components: {
    CopyEditPage
  },
  computed: {
    ...mapGetters(["queryParamsInfo", "copyEditListInfo"])
  },
  methods: {
    ...mapActions(["GetCopyEditListInfo"]),
    autoAddTableInx,
    getDefaultTime,
    goToNewPage,

    handleClickTest() {
      this.isShowEdit = true;
    },
    // 列表信息查询
    handleQueryInfo(params = {}, isFn) {
      // 判断是否为功能参数
      if (isFn) {
        // 获取保存的查询参数
        this.queryData = JSON.parse(this.queryParamsInfo["copy_edit_list"]);
      }

      let obj = {
        spu: this.queryData.productSPU, //SPU
        spu_name: this.queryData.productName, //SPU名称
        supplier_code: this.queryData.supplierCode, //供应商代码
        supplier_name: this.queryData.supplierName, //供应商名称
        category_id: this.queryData.productList, //分类
        function: this.queryData.functionInfo, //职能
        audit_status: this.queryData.auditState, //审核状态
        operator: this.queryData.workUser, //操作人员
        operation_use_time: "", //操作用时
        operation_time_start: this.queryData.operateTime[0], //操作开始
        operation_time_end: this.queryData.operateTime[1], //操作结束
        query_type: this.queryData.moduleType //1 待分配  2 待编辑  3 已编辑
      };

      this.GetCopyEditListInfo(obj)
        .then(({ data }) => {
          console.log(data, 99);

          this.handleQueryInfoCallBack(data);
        })
        .catch(err => {
          console.log(err);
        });
    },

    // 查询重置
    handleResetQueryInfo() {
      this.queryData = {
        moduleType: this.queryData.moduleType,
        productSPU: "",
        supplierCode: "",
        productList: [],
        workUser: "",
        functionInfo: "",
        auditState: "",
        operateUseTime: "",
        productName: "",
        supplierName: "",
        operateUser: "",
        operateTime: []
      };
    },

    //高级筛选
    handleClickFilter() {
      this.filterShow = !this.filterShow;
    },

    animation(isHide) {
      if (isHide) {
        console.log(document.querySelector("body"));
        document.querySelector("body").style.overflow = "hidden";
        return;
      }
      this.$refs.myTable.handleRefreshTableHeight();
    },

    // 分页条数切换
    handleSizeChange(val) {
      this.handleQueryInfo(
        {
          limit: val,
          offset: 1
        },
        true
      );
    },

    // 跳转页数切换
    handleCurrentChange(val) {
      this.handleQueryInfo(
        {
          limit: this.pageData.currentList,
          offset: val
        },
        true
      );
    },

    // 保存勾选数据
    handleSaveCheckInfo(val) {
      this.saveCheckInfo = val;
    },

    // 切换表格列表
    handleToggleTableInfo(val) {
      this.queryData.moduleType = val;
      this.handleQueryInfo();
    },

    // 导出明细信息
    handleExportListInfo() {
      this.handleQueryInfo(
        {
          isExcel: 1
        },
        true
      );
    },

    // 保存上传文件
    handleSaveFileList(file, fileList) {
      this.saveFileList = [...fileList];
    },

    // 选择发货地址
    handleSelectDeliver(val) {
      let obj = {
        province_code: val[0]
      };
      for (let item of this.optionList.deliverAddressList) {
        if (item.value === val[0] && item.children.length) return;
      }
      getCityInfo(obj).then(({ data }) => {
        this.optionList.deliverAddressList.map(value => {
          if (value.value === val[0]) {
            value.children = [...data.data];
          }
        });
      });
    },

    // 预览图片
    handleLookImage(src) {
      this.isShowLookImage = true;
      this.lookImageUrl = [
        "http://pic10.nipic.com/20101016/2531170_233023137000_2.jpg"
      ];
    },

    // 批量创建
    handleSupplierBatchCreate() {
      goToNewPage(
        this,
        {
          pageMain: "/supplier_create_list",
          pageUrl: "/batch_create_supplier",
          pageName: "批量创建"
        },
        "_new"
      );
    },

    // 点击编辑
    handleClickEdit({ supplier_id }) {
      let obj = {
        supplier_id
      };
      getEditInfo(obj).then(({ data }) => {
        // 加载地址
        this.handleSelectDeliver([data.data.data.province_code]);

        this.saveCheckId = supplier_id;
        this.isEditShow = true;
        this.editData.comCode = data.data.data.credit_code;
        this.editData.supplierName = data.data.data.supplier_name;
        this.editData.applyAddress = data.data.data.reg_address;
        this.editData.applyDate = data.data.data.reg_time;
        this.editData.companyType = data.data.data.type;
        this.editData.supplierLink = data.data.data.sup_url;
        this.editData.pAndCity = [data.data.data.province_code, "1000"];
        this.editData.area = data.data.data.shipping_address;
        this.editData.contacts = data.data.data.contacts;
        this.editData.phoneNumber = data.data.data.mobile;
        this.editData.contactsPhone = data.data.data.phone;
        this.editData.wxNumber = data.data.data.wechat;
        this.editData.qqNumber = data.data.data.qq;
        this.editData.email = data.data.data.email;
        this.editData.paymentType = data.data.data.pay_method;
        this.editData.settlementType = data.data.data.settle_method;
        this.editData.point = data.data.data.tax_point;
        this.editData.devUser = data.data.data.devlop_user;
        this.editData.auditUser = data.data.data.check_user;
        this.editData.importDate = data.data.data.create_time;
        this.editData.gatheringAccount = data.data.data.receive_account;
        this.editData.bankName = data.data.data.receive_bank;
        this.editData.gatheringName = data.data.data.receive_name;
        this.editData.gatheringId = data.data.data.receive_id_number;
        this.editData.gatheringPhone = data.data.data.receive_mobile;
        this.editData.shopName = data.data.data.shop_name;
      });
    },

    // 关闭编辑
    handleCloseEdit() {
      for (let key in this.editData) {
        if (Array.isArray(this.editData[key])) {
          this.editData[key] = [];
        } else {
          this.editData[key] = "";
        }
      }
      this.$refs.editData.resetFields();
    },

    // 保存编辑信息
    handleSaveEditInfo() {
      let obj = {
        supplier_id: this.saveCheckId,
        credit_code: this.editData.comCode,
        supplier_code: this.editData.supplierName,
        supplier_name: this.editData.supplierName,
        settle_method: this.editData.settlementType,
        tax_point: this.editData.point,
        type: this.editData.supplierType,
        devlop_user: this.editData.devUser,
        status: this.editData.state,
        // modify_time: this.editData.aaaa,
        web_link: this.editData.supplierLink,
        contacts: this.editData.contacts,
        mobile: this.editData.contactsPhone,
        phone: this.editData.phoneNumber,
        wechat: this.editData.wxNumber,
        qq: this.editData.qqNumber,
        email: this.editData.email,
        shipping_province: this.editData.pAndCity[0],
        shipping_city: this.editData.pAndCity[1],
        shipping_address: this.editData.area,
        pay_method: this.editData.paymentType,
        shop_name: this.editData.shopName,
        receive_name: this.editData.gatheringName,
        receive_account: this.editData.gatheringAccount,
        receive_mobile: this.editData.gatheringPhone,
        receive_id_number: this.editData.gatheringId,
        receive_bank: this.editData.bankName
        // bl_url: this.editData.aaaa,
        // tca_url: this.editData.aaaa,
        // gtc_url: this.editData.aaaa,
        // b_r_rul: this.editData.aaaa,
        // reg_address: this.editData.aaaa,
        // reg_time: this.editData.aaaa,
      };

      saveEditInfo(obj).then(({ data }) => {
        console.log(data);
      });
    },
	// 格式化用戶列表數據
	formatMemberUser(originSource){
		let targetSource = {}
		for(let key in originSource){
			targetSource = {
				...targetSource,
				[key]: originSource[key]['user_name']
			}
		}
		return targetSource;
	},
    //查询回调
    handleQueryInfoCallBack(data) {
	// 保存查询参数
	this.$store.commit("SAVE_QUERY_PARAMS_INFO", {
		key: "copy_edit_list",
		value: JSON.stringify(this.queryData)
	});

	if ('select_list' in data) {
		// 渲染下拉
        this.optionList.productList = [...data.select_list.category_list];
        this.optionList.functionInfoList = data.select_list.function;
		this.optionList.statusList = data.select_list.status;
		this.optionList.workerList = this.formatMemberUser(data.member_user);

        //数据渲染
        this.tableData = data.data.value;
        this.tableKey = data.data.key;
        // changeTableInfoProps(this.tableData, 'status', 'status_cn', this.optionList.stateList, 'value', 'label');
        this.tableProps = [
          "index",
          "spu",
          "photo_path",
          "good_name",
          "category_path",
          "sample_type",
          "good_info",
          "product_status",
          "opera_log",
          "opera_time",
          "remarks"
        ];
        this.tableWidth = [
          "60",
          "150",
          "120",
          "good_name",
          "category_path",
          "sample_type",
          "good_info",
          "100",
          "200",
          "100",
          "remarks"
        ];

        //渲染分页
        this.pageData.currentList = data.page.limit * 1;
        this.pageData.currentPage = data.page.offset * 1;
        this.pageData.currentTotal = data.page.total * 1;
      }
    },

    //是否勾选
    checkedStatus(obj) {
		if (!this.saveCheckInfo.length) {
			this.$message.warning("请勾选数据！");
			return;
		}
		this[obj] = true;
    },

    //指派任务
    handleClickAssign() {
		this.checkedStatus("isShowAssign");
		this.assignType = 1;
    },
    // 重新指派任务
    handleClickReAssign() {
		this.checkedStatus("isShowAssign");
		this.assignType = 2;
    },
    //勾选的id
    selectId() {
    	return this.saveCheckInfo.map(item => item.spu[0]);
    },

    //确认指派
    handleConfirmAssign(val) {
		const { check: assigned_user } = val;
		const spu_list = this.selectId();

		if (!assigned_user) {
			return this.$message.warning("请选择指派人员!");
		}
		if (this.assignType === 1) {
			// 指派人员
			assignSpu({ assigned_user, spu_list }).then(({ data }) => {
				if (data.status !== 1) {
					return this.$message.error(data.errorMess);
				}
				this.$message.success('指派成功！');
			});
		}else if (this.assignType === 2) {
			// 重新指派人员
			reAssignSpu({ assigned_user, spu_list }).then(({ data }) => {
				if(data.status !== 1){
					return this.$message.error(data.errorMess);
				}
				this.$message.success('指派成功！');
			});
		}
    },

    // 驳回
    handleClickReject() {
      this.checkedStatus("isShowReject");
    },

    // 确认驳回
    handleConfirmReject(val) {
      console.log(val);
    },

    // 提交
    handleClickSubmit() {
      if (!this.saveCheckInfo.length) {
        this.$message.warning("请勾选数据！");
        return;
      }
      this.$confirm("确认提交？", "提示", {
        type: "warning"
      }).then(() => {});
    },

    //小组分类
    searchGroup(value) {
      console.log(value);
    }
  },
  created() {
    this.thisVm = this;

    try {
      if (JSON.stringify(this.copyEditListInfo) === "{}") {
        this.handleQueryInfo({ is_get_base: 1 });
      } else {
        // 获取保存的查询参数
        this.queryData = JSON.parse(this.queryParamsInfo["copy_edit_list"]);
        this.handleQueryInfoCallBack(this.copyEditListInfo);
      }
    } catch (e) {
      console.log(e);
    }
  }
};
</script>

<style lang="less" scoped>
@upload-height: 185px;
@border-color: #aaa;

.ui-table--supplier_code {
  font-size: 12px;
  line-height: 10px;
  p:nth-child(1) {
    font-weight: bold;
  }

  p {
    margin-bottom: 8px;
  }
}

/deep/ .el-table td,
/deep/ .el-table th {
  overflow: hidden;
  text-align: left !important;
}

.ui-table--supplier_code--flag {
  width: 50px;
  height: 35px;
  line-height: 54px;
  text-align: center;
  -webkit-transform: rotateZ(45deg);
  transform: rotateZ(45deg);
  background: #63c4e9;
  position: absolute;
  font-size: 12px;
  color: #ffffff;
  top: -12px;
  right: -20px;
  font-weight: normal;
}

.ui-table--icon_btn {
  i {
    cursor: pointer;
    font-size: 14px;
    color: #6facff;
  }
}

.ui-com_edit .ui-com_edit--content--item .ui-com_edit__upload {
  li {
    width: 130px;
    /*height: 185px;*/
    margin-right: 20px;
    text-align: center;
    position: relative;

    img {
      width: 100%;
      height: @upload-height;
    }

    .ui-com_edit__upload--last_btn {
      border: 1px solid #e6e6e6;
      width: 100%;
      height: @upload-height;

      /deep/ .el-upload {
        display: block;
        height: @upload-height;
        line-height: @upload-height;
        background: #F5F9F8;

        span {
          color: @border-color;
          font-size: 34px;
          margin-right: 0;
          position: relative;
          top: -8px
        }
        p {
          position: absolute;
          left: 33px;
          top: 20px;
          font-size: 12px;
          color: #999999;
        }
      }
    }

    .ui-com_edit__upload--item--mask {
      display: none;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: @upload-height;
      background: rgba(0, 0, 0, 0.3);
      text-align: center;
      line-height: 185px;

      & > div {
        display: inline-block;
      }

      span {
        margin: 0 5px;
        cursor: pointer;
        color: #cccccc;
        font-size: 20px;

        &:hover {
          color: #ffffff;
        }
      }
    }

    &:hover {
      .ui-com_edit__upload--item--mask {
        display: block;
      }
    }
  }
}
</style>



