/**  --->>>>   |^ 检查清单^|   <<<<---**/

import http from '@/http'
import CONFIG from '@/assets/js/config'

// 获取检查清单列表
export function getTestListInfo (queryData = {}) {
    let data = {
        limit: 20,
        offset: 1,
        ...queryData
    }

    return http.getAjax('/sample_config/qc_config/config_list', data)
}

// 导出excel
export function exportTestListInfo (params = {}) {
    const data = {
        ...params
    }
    let str = '?'
    for (let key in data) {
        str = `${str}&${key}=${data[key] || ''}`
    }

    window.open('/sample_config/qc_config/export_config' + str, '_self')

}

//删除检查清单
export function delTestList (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }

    return http.postAjax('/sample_config/qc_config/del_config', data)
}

// 修改检查清单
export function changeTestList (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    }

    return http.postAjax('/sample_config/qc_config/edit_config', data)
}

//新增包装信息
export function addTestList (params = {}) {
    const data = {
        ...CONFIG.ajaxData,
        ...params
    };

    switch (params.fid) {
        case '5':
            return http.postAjax('/sample_config/return_config/add_config', data);
        case '6':
            return http.postAjax('/sample_config/quality_config/add_config', data);
        default:
            return http.postAjax('/sample_config/sample_config/add_config', data);

    }
}

// 排序
export function sortEdit (params) {
    console.log(params)
    return http.postAjax('/sample_config/qc_config/edit_order_by', params)
}





